import Link from 'next/link'

const Header = () => (
  <header>
    <div className="grid">
      <div className="col-1-1 floatLeft">
        <div id="logo">
          <img src="../static/images/pcdLogo.png" />
        </div>
        <nav id="navigation" className="mainNav">
          <ul className="menu">
            <li>
              <Link href="/">
                <a className='active'>!=null</a>
              </Link>
            </li>
            <li>
              <Link href="/about">
                <a>about</a>
              </Link>
            </li>
          </ul>
        </nav>
      </div>
    </div>
  </header>
)

export default Header